
.welcome-page {
    height: 100vh;

    div.center-nav {
        position: fixed;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        height: 200px;
        margin: auto;

        button.open-link {
            margin: 8px 0;
            height: 44px;
            background-color: #3990f1;
            border-radius: 4px;
            outline: none;
            border: 0;
            color: #fff;
        }
    }

}

.confirm-form {
    input {
        display: block;
        width: 80%;
        outline: none;
        border: 1px solid #ccc;
        height: 34px;
        margin: 8px auto;
    }
}

.modal-bottom {
    margin-top: 4px;
    text-align: right;

    button {
        padding: 4px 12px;
        border: none;
        background-color: #3990f1;
        color: #fff;
        border-radius: 4px;
        margin: 0 8px;
    }
}
