
.modal-view-popup {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.315);
    z-index:999;
    .pop-up-content {
        position: absolute;
        left: 0;
        right: 0;
        top: 40%;
        margin: auto;
        background-color: #fff;
        padding: 16px;

        .modal-header {

            text-align: right;
            padding: 8px 16px;
        }

        .modal-bottom {
            margin-top: 4px;
            text-align: right;

        }


    }
}
