
.default-page {
    margin-top: 4px;
}

p.page-welcome {
    margin: 10px 4px;
}

.chat-scroll-list {
    scroll-behavior: smooth;
    overflow-y: scroll;
    font-family: "Microsoft Yahei,Arial", "KaiTi", "宋体", sans-serif, Helvetica;
    margin-bottom: 8px;
    box-shadow: 1px 3px 10px 3px #ccc;
    padding: 4px 0 20px 0;
    box-sizing: border-box;
}

.chat-send-footer {
    position: fixed;
    display: flex;
    left: 12px;
    right: 12px;
    bottom: 2px;
    flex-direction: column;

    .input-area {
        outline: none;
        border-radius: 4px 0 0 4px;
        line-height: 22px;
        padding: 8px 6px;
        border: 1px solid #ddd;

        &:focus {
            border: 1px slateblue solid;
        }
    }

    .chat-submit-btn {
        height: 48px;
        border: 0;
        background-color: rgb(38, 78, 255);
        color: #fff;
        border-radius: 0 4px 4px 0;

        &.disalbed-btn {
            background-color: skyblue;
        }
    }

    .chat-voice-btn {
        height: 48px;
        border: 0;
        background-color: rgb(53, 53, 53);
        color: #fff;
        border-radius: 0 4px 4px 0;
    }

    .voiceList {
        list-style: none;
        padding-left: 0;

        li {
            display: flex;
            align-items: center;
        }
    }
}
