
.chat-detail-item {
    margin: 8px 0;
    display: flow-root;
}

.chat-from {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    .from-user {
        margin-right: 6px;
    }
}

.chat-owner {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.from-user {
    font-size: 14px;
    font-weight: 600;
    font-family: cursive;
}

.send-time {
    font-size: 12px;
    color: #a7a7a7;
    margin-right: 8px;
}



.content-other,
.content-owner {
    padding: 2px 6px;

    .content-desc {
        word-break: break-all;
        word-wrap: break-word;
        font-size: 18px;
    }
}

.content-owner {
    float: right;
}

.content-other {
    float: left;
    &:hover{
        background-color: lightblue;
    }
    .botContentList {
        padding-left: 6px;
        margin: 0;
        list-style: none;
    }
}
